.app{
  min-height: 100vh;
  background: rgb(16, 189, 174);
  display: flex;
  justify-content: space-around;
  align-items: center;
}


.tweet{
  height: 300px;
  width: 300px;
  background: white;
  border-radius:30px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.red{
  color: red;
  font-size: 50px;
}

#main2{
  background:  #eff0f1; 
  border: solid 2px darkgray;
}

